/* Common class styles used across all app */
.h-flex {
    display: flex;
    flex-wrap: wrap;
}
.h-flex.jc {
    justify-content: center;
}
.h-flex.ac {
    align-items: center;
}
.h-flex.sb {
    justify-content: space-between;
}

.v-flex {
    display: flex;
    flex-wrap: wrap;
}
.v-flex.ac {
    align-items: center;
}
.text-center {
    text-align: center !important;
}
.text-right {
    text-align: right !important;
}
/**************************************/

/* App styles */
/* .app {
    text-align: center;
} */

.app-header {
    padding: 20px;
    margin: auto;
    min-height: 150px;
}

.search-input {
    width: 50%;
    margin: 20px;
    border-radius: 2px;
    border: none;
    padding: 6px;
}

.app-content {
    padding: 30px;
}

.app-footer {
    clear: both;
    position: relative;
    height: 50px;
    margin-top: -50px;
    background-color: #303f9f;
    color: #fff;
    text-align: right;
    line-height: 50px;
    padding: 0 50px;
    font-size: 13px;
}


.content-not-found {
    text-align: center;
    padding: 30px;
}

.query {
    font-size: 10px;
    background: grey;
    padding: 4px;
    border-radius: 2px;
    margin: 0 5px;
}

.popup-container-enter.image-popup {
    opacity: 0;
}
.popup-container-enter.popup-container-enter-active.image-popup {
    opacity: 1;
    transition: all 0.4s ease-in-out;
}
.popup-container-leave {
    opacity: 1;
}
.popup-container-leave.popup-container-leave-active.image-popup {
    opacity: 0;
    transition: all 0.2s ease-in-out;
}

.image-popup-container {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

.popup-image {
    cursor: pointer;
    -webkit-box-shadow:  0px 0px 0px 9999px rgba(0, 0, 0, 0.8);
    box-shadow:  0px 0px 0px 9999px rgba(0, 0, 0, 0.8);
}

.image-item {
    animation: all 0.3s ease-in-out;
    height: 250px;
}

.image-metadata {
    font-size: 13px;
    transition: all 0.3s ease-in-out;
    margin: 20px;
    color: #FFF;
}

.image-pagination {
    margin: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}